<template>
  <div id="app" class="content" ref="addImage">
    <div class="onFlex">
      <div ref="capture" class="image-content">
        <img class="imgTitle" src="./img/详细体检项目.jpg" alt="" />
        <div class="TJXM" :style="{ background: tableTitleBgColor }">
          <p class="TJXM_XM">项目</p>
          <p class="TJXM_ZB">项目内容</p>
          <p v-if="deleteTJXM" class="TJXM_CZ">操作</p>
        </div>
        <div
          class="TJXMContent"
          v-bind:class="{ ifBGColor: index % 2 === 1 }"
          v-for="(item, index) in TJXM"
          v-bind:key="index"
        >
          <p class="TJXMContent_XM" :style="{ color: titleColor }">
            {{ item.项目 }}
          </p>
          <div class="line"></div>
          <p class="TJXMContent_ZB">{{ item.指标 }}</p>
          <button
            class="TJXMContent_CZ"
            v-if="deleteTJXM"
            v-bind:index="index"
            @click="removeTJXM(index)"
          >
            删除
          </button>
        </div>
      </div>
      <div class="inputBoxAll">
        <div class="inputBox">
          <h3 class="inputBox_title">详细体检项目</h3>
          <p>
            项&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;目：
            <input type="text" v-model="xiangmu" v-bind:name="'项目'" />
          </p>
          <p>
            项目内容：
            <textarea
              type="text"
              v-model="zhibiao"
              v-bind:name="'指标'"
              rows="7"
            />
          </p>
          <button @click="addTJXMBtn">加入</button>
        </div>
        <div class="colorSetting">颜色设定</div>
        <div class="colorInput">
          表头背景颜色
          <input type="text" v-model="tableTitleBgColor" />
        </div>
        <div class="colorInput">
          项目标题颜色 <input type="text" v-model="titleColor" />
        </div>
        <div
          @click="generatorImage"
          class="img-btn"
          style="width: 1rem;
          height: .5rem;
          font-size: .2rem;
          text-align: center;
          line-height:.5rem;
          background: blue;
          color: #FFFFFF;
          border-radius: .2rem;
          cursor: pointer;
          margin-top: .1rem"
        >
          生成图片
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import html2canvas from "html2canvas";

export default {
  name: "Img1",
  data() {
    return {
      deleteTJXM: true,
      TJXM: [],
      xiangmu: "", //项目
      zhibiao: "", //指标
      titleColor: "#66aaff", //标题
      tableTitleBgColor: "#66aaff" //表格标题
    };
  },
  methods: {
    generatorImage() {
      let that = this;
      this.titleColor === "" ? (this.titleColor = "#66aaff") : this.titleColor;
      this.tableTitleBgColor === ""
        ? (this.tableTitleBgColor = "#66aaff")
        : this.tableTitleBgColor;
      function onPromise() {
        return new Promise(resolve => {
          that.deleteTJXM = false;
          window.scrollTo(0, 0);
          resolve();
        })
          .then(() => {
            html2canvas(that.$refs.capture).then(canvas => {
              that.$refs.addImage.append(canvas);
              let link = document.createElement("a");
              link.href = canvas.toDataURL();
              link.setAttribute("download", "图片canvas.png");
              link.style.display = "none";
              document.body.appendChild(link);
              link.click();
            });
          })
          .then(() => {
            that.deleteTJXM = true;
          });
      }

      onPromise();
    },
    addTJXMBtn() {
      let that = this;
      if (that.xiangmu && that.zhibiao) {
        let data = that.TJXM;
        data.push({
          项目: that.xiangmu,
          指标: that.zhibiao,
          功能建议: that.yijian
        });
        that.TJXM = data;
        that.xiangmu = "";
        that.zhibiao = "";
      } else {
        alert("输入框不能为空！！");
      }
    },
    removeTJXM(index) {
      let that = this;
      let data = [];
      that.TJXM.map((item, i) => {
        if (i !== index) {
          data.push(item);
        }
      });
      that.TJXM = data;
    }
  }
};
</script>

<style type="text/css">
@import "img1.css";
</style>
