<template>
  <div id="app" class="content" ref="addImage">
    <div class="onFlex">
      <div ref="capture" class="image-content">
        <img class="imgTitle" src="./img/详细体检项目.jpg" alt="" />
        <ul class="content_title_box">
          <li
            class="content_title_keshi border_radius_left"
            :style="tableTitleBgColor"
          >
            科室
          </li>
          <li class="content_title_neirong">
            <ul class="content_title_neirong_box">
              <li
                class="content_title_neirong_xiangmu"
                :style="tableTitleBgColor"
              >
                项目
              </li>
              <li
                class="content_title_neirong_linchuangyiyi border_radius_right"
                :style="tableTitleBgColor"
              >
                临床意义
              </li>
              <li
                class="content_title_neirong_delete"
                v-if="ifDeleteBtn"
                :style="tableTitleBgColor"
              >
                操作
              </li>
            </ul>
          </li>
        </ul>
        <ul
          class="content_neirong_box"
          v-bind:class="{ ifBgColor: upindex % 2 }"
          v-for="(item, upindex) in datas"
          v-bind:key="'datas-' + upindex"
        >
          <li class="content_neirong_keshi" :style="keshiTextTitle">
            {{ item.keshi }}
          </li>
          <li class="content_neirong_center">
            <ul
              class="content_neirong_center_ul"
              v-for="(item, index) in item.list"
              v-bind:key="'datas_list-' + index"
            >
              <li class="content_neirong_center_xiangmu">{{ item.xiangmu }}</li>
              <li class="content_neirong_center_linchuangyiyi">
                {{ item.linchuangyiyi }}
              </li>
              <li
                class="content_neirong_center_delete"
                v-if="ifDeleteBtn"
                v-bind:index="index"
                v-bind:upindex="upindex"
                v-on:click="deleteTable"
              >
                删除
              </li>
            </ul>
          </li>
          <li class="content_neirong_center_biaozhu" :style="biaozhuBgColor">
            {{ item.biaozhu }}
          </li>
        </ul>
      </div>
      <div class="inputBoxAll">
        <div class="input_Box">
          <div class="input_keshi">
            <input type="text" placeholder="科室" v-model="list.editKeshi" />
          </div>
          <div class="input_content">
            <div
              class="input_content_center"
              v-for="(item, index) in list.list"
              v-bind:key="'input' + index"
            >
              <input
                type="text"
                placeholder="项目"
                v-bind:index="index"
                v-model="list.list[index].editXiangmu"
              />
              <input
                type="text"
                placeholder="临床意义"
                v-bind:index="index"
                v-model="list.list[index].editLinchuangyiyi"
              />
              <button
                class="deleteInput"
                v-bind:index="index"
                v-on:click="deleteInput"
              >
                删除
              </button>
            </div>
          </div>
          <button class="addInput_btn" v-on:click="addInput">添加</button>
        </div>
        <div class="input_biaozhu">
          <input type="text" placeholder="标注" v-model="list.editBiaozhu" />
        </div>
        <div class="addTableBtn" v-on:click="addTable">插入表格</div>
        <div class="colorSetting">颜色设定</div>
        <div class="colorInput">
          表头背景颜色
          <input type="text" v-model="tableTitleBgColor.background" />
        </div>
        <div class="colorInput">
          科室标题颜色 <input type="text" v-model="keshiTextTitle.color" />
        </div>
        <div class="colorInput">
          注释背景颜色 <input type="text" v-model="biaozhuBgColor.background" />
        </div>
        <div @click="generatorImage" class="img-btn">生成图片</div>
      </div>
    </div>
  </div>
</template>

<script>
import html2canvas from "html2canvas";

export default {
  name: "Img2",
  data() {
    return {
      datas: [], //显示内容
      list: {
        //编辑内容
        editKeshi: "", //科室
        list: [
          {
            editXiangmu: "", //项目
            editLinchuangyiyi: "" //临床意义
          },
          {
            editXiangmu: "", //项目
            editLinchuangyiyi: "" //临床意义
          }
        ],
        editBiaozhu: "" //标注
      },
      ifDeleteBtn: true, //是否显示删除按钮
      keshiTextTitle: { color: "#66aaff" }, //科室文字颜色
      tableTitleBgColor: { background: "#66aaff" }, //表头背景颜色
      biaozhuBgColor: { background: "#fbb74c" } //标注背景颜色
    };
  },
  methods: {
    //生成图片
    generatorImage() {
      let that = this;
      this.keshiTextTitle.color === ""
        ? (this.keshiTextTitle.color = "#66aaff")
        : this.keshiTextTitle.color;
      this.tableTitleBgColor.background === ""
        ? (this.tableTitleBgColor.background = "#66aaff")
        : this.tableTitleBgColor.background;
      this.biaozhuBgColor.background === ""
        ? (this.biaozhuBgColor.background = "#fbb74c")
        : this.biaozhuBgColor.background;

      function onPromise() {
        return new Promise(resolve => {
          that.ifDeleteBtn = false;
          window.scrollTo(0, 0);
          resolve();
        })
          .then(() => {
            html2canvas(that.$refs.capture).then(canvas => {
              // that.$refs.addImage.append(canvas);
              let link = document.createElement("a");
              link.href = canvas.toDataURL();
              link.setAttribute("download", "图片canvas.png");
              link.style.display = "none";
              document.body.appendChild(link);
              link.click();
            });
          })
          .then(() => {
            that.ifDeleteBtn = true;
          });
      }

      onPromise();
    },
    //添加input框
    addInput() {
      window.console.log(this.ifDeleteBtn);
      let that = this;
      let list = that.list;
      list.list.push({
        editXiangmu: "", //项目
        editLinchuangyiyi: "" //临床意义
      });
    },
    // 删除input框
    deleteInput(e) {
      let that = this;
      let listIndex = e.target.getAttribute("index");
      let list = that.list;
      let result = [];
      if (list.list.length <= 1) {
        alert("最少留一个吧！");
        return;
      }

      list.list.map((item, index) => {
        if (index !== Number(listIndex)) {
          result.push(item);
        }
      });

      that.list.list = result;
    },
    // 插入表格
    addTable() {
      let that = this;
      let result = {
        keshi: "", //科室
        list: [],
        biaozhu: ""
      };
      result.keshi = that.list.editKeshi;
      result.biaozhu = that.list.editBiaozhu;
      that.list.list.map(item => {
        result.list.push({
          xiangmu: item.editXiangmu, //项目
          linchuangyiyi: item.editLinchuangyiyi //临床意义
        });
      });
      that.datas.push(result);
      window.console.log(that.datas);
    },
    //删除表格
    deleteTable(e) {
      let listIndex = e.target.getAttribute("index");
      let upindex = e.target.getAttribute("upindex");
      let data = this.datas;
      let Olist = [];
      // 查找删除的一行
      data[upindex].list.map((item, index) => {
        if (index !== Number(listIndex)) {
          Olist.push(item);
        }
      });
      // 判断是否还有项目
      if (Olist.length <= 0) {
        //没有删除科室
        let datas = [];
        data.map((item, index) => {
          if (index !== Number(upindex)) {
            datas.push(item);
          }
        });
        this.datas = datas;
      } else {
        //有则保留科室
        this.datas[upindex].list = Olist;
      }
    }
  }
};
</script>

<style scoped type="text/css">
@import "img2.css";
</style>
